body{
  background-color: black;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

#imgDiv{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
img{
  z-index: -1;
  position: relative;
  left: 0;
  top: 0;
  min-height: 100%;
}

#Main{
  z-index: 1;
  padding: 10px;
  color: white;
  border-radius: 10px;
  background-color: rgba(48, 48, 48, 0.788);
}
  #form{
    margin: 10px;
  }
    #form div{
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      gap: 10px;
    }

#hotMilfs{
  width: 320px;
  height: 120px;
  background-color: white;
  border-radius: 10px 0 0 10px;
  position: absolute;
  top: 20px;
  right: 0;
  opacity: 0;
  overflow: hidden;
}
  #hotMilfs div{
    position: absolute;
    margin-left: 140px;
    text-align: left;
    padding: 10px;
  }
  #hotMilfs img{
    position: absolute;
    left: 0;
    z-index: 0;
    width: 140px;
    height: 100%;
  }
  #hotMilfs button{
    position: absolute;
    bottom: 0px;
    right: 0px;
  }

.colorGreen{
  color: rgb(0, 245, 0);
}

button{
  padding: 5px;
  border: 2px solid rgb(0, 255, 0);
  color: white;
  background-color: green;
}

@keyframes opacity {
  0%{opacity: 0; transform: translateX(100%);}
  100%{opacity: 1; transform: translateX(0%)}
}